import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CardContent from "@material-ui/core/CardContent";
import Avatar from "@material-ui/core/Avatar";
import Typography from "@material-ui/core/Typography";
import { red } from "@material-ui/core/colors";
import { showDate } from "./helper";

const useStyles = makeStyles((theme) => ({
  card: {
    width: "93%",
    marginLeft: "5%",
    marginTop: "10px",
    // maxHeight: "30%",
    overflowY: "auto",
  },
  avatar: {
    backgroundColor: red[500],
  },
  header: {
    backgroundColor: theme.palette.primary,
    color: "white",
  },
}));

export default function MessageContent(props) {
  const classes = useStyles();
  return (
    <Card className={classes.card}>
      <CardHeader
        avatar={
          <Avatar className={classes.avatar}>
            {props.message.assignee
              ? `${props.message.assignee.firstName.charAt(
                  0
                )}${props.message.assignee.lastName.charAt(0)}`
              : "?"}
          </Avatar>
        }
        title={
          <Typography variant="h6" color="primary">
            {props.message.assignee
              ? `${props.message.assignee.firstName} ${props.message.assignee.lastName}`
              : "?"}
          </Typography>
        }
        subheader={
          <Typography color="secondary">
            {showDate(props.message.createdDate)}
          </Typography>
        }
        className={classes.header}
      />
      <CardContent>
        <div dangerouslySetInnerHTML={{ __html: props.message.body }} />
      </CardContent>
    </Card>
  );
}
